// import { Box} from "@react-three/drei";
import React from "react";
import { useThree, useFrame } from "@react-three/fiber";
import TWEEN from "@tweenjs/tween.js";
import * as THREE from "three";

const ChangeCamera = props => {
    const { step } = props;
    // let camTarget = new THREE.Vector3(0, 0, 0);
    const {camera, scene } = useThree();
    camera.fov = 60;
    camera.near = 0.1;
    camera.far = 1000;
    // camera.rotation.x = 1.16;
    // camera.rotation.y = -0.12;
    // camera.rotation.z = 0.27;
    // const cameraInitPos = { x: 0, y: 200, z: 450 };
    // camera.lookAt(0, -40, -20);
    //const cameraInitPos = { x: 0, y: 150, z: 400 };
    const cameraInitPos = { x: 0, y: 100, z: 400 };
    let targetBox = scene.getObjectByName("cameraTarget");
    let targetPos = new THREE.Vector3(0, 0, 0);
    let tPos = targetBox ? targetBox.position : new THREE.Vector3(0, 0, 0);
    console.log(step, '. targetBox position: ', camera, tPos);
    camera.lookAt(tPos);

    if (step === 0) {
        targetPos = new THREE.Vector3(0, 0, 0);
        tweenCamera(camera, cameraInitPos, targetBox, targetPos);
    } else if (step === 1) {
        // camTarget = new THREE.Vector3(0, 50, 0);
        targetPos = new THREE.Vector3(0, 50, 0);
        tweenCamera(camera, { x: 0, y: -10, z: 40 }, targetBox, targetPos);
    } else if (step === 2) {
        targetPos = new THREE.Vector3(0, 50, 0);
        tweenCamera(camera, { x: 0, y: -10, z: 40 }, targetBox, targetPos);
    }else if (step === 3) {
        targetPos = new THREE.Vector3(0, 50, 0);
        tweenCamera(camera, { x: 0, y: -10, z: 40 }, targetBox, targetPos);
    }else if (step === 4) {
        targetPos = new THREE.Vector3(0, 50, 0);
        tweenCamera(camera, { x: 0, y: -5, z: 70 }, targetBox, targetPos);
    } else if (step === 5) {
        targetPos = new THREE.Vector3(0, 0, 0);
        tweenCamera(camera, { x: 0, y: 60, z: 250 }, targetBox, targetPos);
    }

    camera.updateProjectionMatrix();
    useFrame(state => {
        TWEEN.update();
        camera.lookAt(targetBox.position);
        // camera.lookAt(camTarget);
    });

    return null;
};

function tweenCamera(camera, cameraPos, targetBox, targetPos) {    
    let pos = camera.position;
    let tPos = targetBox ? targetBox.position : new THREE.Vector3(0, 0, 0);
    console.log("tweencamera, camera Pos: ", pos);

    new TWEEN.Tween(pos)
        .to({ x: cameraPos.x, y: cameraPos.y, z: cameraPos.z }, 1000)
        .easing(TWEEN.Easing.Quadratic.Out)
        .onUpdate(function() {})
        .start();

    new TWEEN.Tween(tPos)
        .to({ x: targetPos.x, y: targetPos.y, z: targetPos.z }, 1000)
        .easing(TWEEN.Easing.Quadratic.Out)
        .onUpdate(function() {
            // console.log('tweening camera, xyz: ',pos.x, pos.y, pos.z )
        })
        .start();
}

function UpdateCamera(props) {
    const { step } = props; // step
    return (
        <ChangeCamera step={step} />
    );
}

export default UpdateCamera;
