import React, { useState, useRef } from "react";


function Timeline(prop){
    let {step} = prop;
    console.log("Timeline", step);
    let [dateLabel, setDateLabel] = useState("Mar. '20");
    let [val, setVal] = useState(0);
    // let {slider, setSlider} = useState(0);
    const timeLabels = ["Mar. '20", "Apr. '20", "May '20", "Jun. '20", "Jul. '20", "Aug. '20", "Sep. '20", "Oct. '20", "Nov. '20", "Dec. '20", "Jan. '21", "Feb. '21",
    "Mar. '21", "Apr. '21", "May '21", "Jun. '21", "Jul. '21", "Aug. '21", "Sep. '21", "Oct. '21", "Nov. '21", "Dec. '21", "Jan. '22", "Feb. '22","Mar. '22"
    ];


    function onChange(ev){
        const val = ev.target.value;
        // ev.value = val;
        setVal(val);
        dateLabel = setDateLabel(timeLabels[val])
        console.log('val', val)
    }

    return (
            <div className="timeline">
                <input type="range" name="DateSlider" id="DateSlider" min="0" max="24" step="1" value={ (!step? 0: (step==5)? timeLabels.length:step ) } onChange={onChange} />
                <label id="dateLabelCtn">{dateLabel}</label>
                <div className='label' id="dateStart">Mar. '20</div>
                <div className='label' id="dateEnd">Mar. '22</div>
            </div>
    );
}

export default Timeline;