import React, { useRef } from "react";
import { useLoader, useFrame } from "@react-three/fiber";
import { TextureLoader } from "three/src/loaders/TextureLoader";
function Cloud(props) {
    // This reference will give us direct access to the THREE.Mesh object
    const mesh = useRef();
    // // Set up state for the hovered and active state
    // const [hovered, setHover] = useState(false);
    // const [active, setActive] = useState(false);
    // // Subscribe this component to the render-loop, rotate the mesh every frame
    useFrame((state, delta) =>
        mesh.current ? (mesh.current.rotation.z -= 0.0005) : null
    );
    const [colorMap] = useLoader(TextureLoader, [
        "./assets/images/smoke-1.png"
    ]);

    return (
        <mesh {...props} ref={mesh} rotation={[1.16, -0.12, 200]} >
            <planeBufferGeometry args={[600, 600]} />
            <meshLambertMaterial opacity={0.3} transparent={true} map={colorMap} />
        </mesh>
    );
}

export default Cloud;
